    .woocommerce ul.products li.product,
    .woocommerce[class*="columns-"] ul.products li.product,
    .woocommerce ul.products[class*="columns-"] li.product {

        position: relative;
        float: none;
        width: 100%;    
        max-width: 100%;

        &::before,
        &::after {

            content: "";
            display: none;
        }
    }
}

