/*
====================================================
=
= Banner styles
=
==========================================
*/
.banner-style-1 .left-col {

    flex: 0 0 66.66%;
    max-width: 66.66%;
}

.banner-style-1 .right-col {

    flex: 0 0 33.33%;
    max-width: 33.33%;
}

.banner-style-1 .banner-image-wrapper {

    position: relative;
    overflow: hidden;
    margin-bottom: 30px;
}

.banner-style-1 .banner-image-wrapper img {

    width: 100%;
    height: 210px;
    object-fit: cover;
    object-position: center;
}

.banner-style-1 .right-col .banner-image-wrapper:last-child {

    margin-bottom: 0;
}

.banner-style-1 .banner-image-wrapper a {

    display: block;
}

.banner-style-1 .button-general {

    padding: 15px 30px;
}

.category-nav ul {

    overflow: hidden;
    list-style: none;
}

.category-nav ul.overflow-visible {

    overflow-y: visible;
}

.banner-style-1 .cat-nav-entry {

    overflow: hidden;
    overflow-y: visible;
}

.category-nav li {

    display: block;
}

.category-nav li a {

    font-size: 16px;
    display: block;
    padding: 14px 30px;
    line-height: 1;
    border-bottom: 1px solid $border_color;
}

.category-nav li:last-child a {

    border-bottom: none;
}

.category-nav li a i {

    padding-right: 10px;
    font-size: 14px;
}

.banner-style-1 .thumb,
.banner-style-1 .item-entry,
.banner-style-1 .category-nav ul {

    position: relative;
    @include bg_image(center center, cover, inherit);
    height: 453px;
}

.banner-style-1 .item-entry {

    position: relative;
}

.banner-style-1 .content-holder {

    @include position_absolute(50%, unset, 0, auto);
    width: 100%;
    padding: 30px 0 30px 40px;
    max-width: 500px;
    z-index: 3;
}

.banner-style-1 .button-general {

    padding: 15px 30px;
}

// mask

.banner-style-1 .thumb .mask {

    z-index: -1;
    background-color: rgba(0, 0, 0, 0.2);
}

.banner-style-1.show-mask .thumb .mask {

    z-index: 2;
}

.banner-style-1.show-mask .entry-contents {

    color: $white;
}

// animation

.banner-style-1 .owl-item.active .caption span {

    animation: 0.9s 0.3s fadeInUp both;
}

.banner-style-1 .owl-item.active h2 {

    animation: 0.9s 0.7s fadeInUp both;
}

.banner-style-1 .owl-item.active .excerpt p {

    animation: 0.9s 1.1s fadeInUp both;
}

.banner-style-1 .owl-item.active .permalink a {

    animation: 0.9s 1.5s fadeInUp both;
}

.banner-style-1 .button-general {

    @include border_radius(0);
}

// preloader ( on window load till document is ready )

.carousel-preloader {

    position: relative;
}

.carousel-preloader .init-preloader {

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    z-index: 100;
    background: whitesmoke;
    overflow: hidden;
    @include transition();
}

.carousel-preloader .init-preloader::before {

    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;
    width: 100%;
    max-width: 100%;
    height: 100%;
    background-image: linear-gradient(90deg, #DDDDDD 0px, #e8e8e8 40px, #DDDDDD 80px);
    background-size: 100%;
    animation: shine-lines 1.6s infinite linear;
}

.banner-style-1 .carousel-preloader {

    height: 453px;
}

/*
====================================================
=
= category layout styles
=
==========================================
*/

.cats-widget-styles .thumb {

    position: relative;
