/*
====================================================
=
= Search page styles
=
==========================================
*/

.search-entry .entry-title,
.archive-entry .entry-title {

    margin-bottom: 30px;
}

.search-entry,
.archive-entry {

    article {

        padding: 30px;

        @include breakpoint('mobile') {

            padding: 20px;
        }

        &:not(.has-post-thumbnail) {

            .os-row {

                column-gap: 0;
                grid-template-columns: repeat(1, 1fr);
            }
        }

        .os-row {

            display: grid;
            column-gap: 30px;
            grid-template-columns: repeat(2, 1fr);
            margin: 0;

            @include breakpoint('right-after-ipad') {

                row-gap: 30px;
                grid-template-columns: repeat(1, 1fr);
            }

            .os-col {

                padding: 0;
                width: 100%;
                max-width: 100%;
            }
        }

        .entry-cats {

            margin-bottom: 15px;
        }
    }
}

