/*
====================================================
=
= WooCommerce styles
=
==========================================
*/

// product listing & hover effects.
.woocommerce {

    .products {

        .product {

            // DEFAULT STYLE         
            .product-main-wrap {

                .product-thumb-wrap {

                    .custom-cart-btn {

                        display: flex;
                        flex-wrap: wrap;
                        align-items: center;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        right: 0;
                        left: 0;
                        width: 100%;
                        bottom: 10px;
                        z-index: 5;
                        margin: 0;
                        padding: 0;

                        .button {

                            margin: 0;
                        }

                        .woocommerce-loop-product__link {

                            display: none;
                        }

                        @include breakpoint('mobile') {

                            display: none;
                        }
                    }
                    
                }

                .product-info-wrap {

                    display: flex;
                    gap: 20px;
                    flex-direction: column;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                    overflow: hidden;

                    .woocommerce-loop-product__link {

                        .woocommerce-loop-product__title {

                            margin: 0;
                        }
                    }

                    .star-rating {

                        margin: 0;
                    }
                }
            }

            // ADD TO CART BUTTON
            .custom-cart-btn {

                margin: 0;

                .button  {

                    margin: 0;
                }
            }

            // WHEN ADD TO CART BUTTON IS DISPLAY ON PRODUCT HOVER.
            &.os-add-to-cart-button-display-on-product-hover {

                @include breakpoint( 'until-ipad-pro' ) {

                    .product-main-wrap {

                        .product-thumb-wrap {

                            .custom-cart-btn {

                                @include visibility('hidden');
                            }
                        }

                        .product-info-wrap {

                            .price,
                            .star-rating,
                            .woocommerce-loop-product__link {

                                @include transition();
                            }

                            .custom-cart-btn {

                                display: flex;
                                gap: 5px;
                                flex-direction: column;
                                align-items: center;
                                justify-content: center;
                                position: absolute;
                                z-index: 5;
                                left: 0;
                                right: 0;
                                bottom: 40px;
                                width: 100%;
                                @include visibility('hidden');
                                @include transition();

                                a.wc-forward {

                                    display: none;
                                }
                            }
                        }
                    }

                    &:not(.os-add-to-cart-button-position-over-thumbnail):hover {

                        .product-main-wrap {

                            .product-info-wrap {

                                .price,
                                .star-rating,
                                .woocommerce-loop-product__link {

                                    @include visibility('hidden');
                                }

                                .custom-cart-btn {

                                    @include visibility('visible');
                                    animation: osfadeInUp 0.5s ease-in-out forwards;
                                }
                            }
                        }
                    }

                    &.os-add-to-cart-button-position-over-thumbnail {

                       &:hover {

                            .product-main-wrap {

                                .product-thumb-wrap {

                                    .custom-cart-btn {

                                        @include visibility('visible');
                                        animation: osfadeInUp 0.5s ease-in-out forwards;
                                    }
                                }
                            }
                       }
                    }
                } 
            }
        }
    }
}

.product-main-wrap {

    position: relative;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-content: space-between;
    overflow: hidden;
    height: 100%;
    background-color: white;
    @include transition();
}

.product-main-wrap:hover {

    @include hover_big_shadow();
}

.product-thumb-wrap {

    position: relative;
    overflow: hidden;

    .woocommerce-loop-product__link {

        display: block;
        position: relative;
        overflow: hidden;

        img {

            display: block;
            position: relative;
            z-index: 1;
            aspect-ratio: auto;
            @include transition( all, 0.6s, ease-in-out );
        }

        .secondary-image {

            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 2;
            @include visibility( 'hidden' );
        }
    }
}

// product hover effect swap.
.os-product-thumbnail-effect-swap {

    &:is(:hover) {

        .product-thumb-wrap {

            .woocommerce-loop-product__link {

                .secondary-image {

                    @include visibility( 'visible' );
                }
            }
        }
    }
}

// proudct hover effect zoom.
.os-product-thumbnail-effect-zoom {

    &:is(:hover) {

        .product-thumb-wrap {

            .woocommerce-loop-product__link {

                img:not(.secondary-image) {

                    transform: scale(1.2);
                }
            }
        }
    }
}

